body {
    background: rgb(251, 251, 251) !important;
    .category {
        font-family: 'Roboto-medium';
        font-weight: 'bold';
        font-size: 20px;
        color: black !important;
        text-decoration-line: underline;
    }

    .sub-category {
        font-family: 'Roboto-medium';
        font-size: 12px;
        color: var(--black) !important;
        text-decoration-line: underline;
    }

    @media (max-width: 760px) {
        .media-center {
            p {
                text-align: center !important;
            }
        }
    }
}
